<script>
// @ is an alias to /src
import { isPC, isMobile, isTablet } from '@/utils'
import slider from '@/mixins/slider.js'

export default {
  name: 'section2',

  mixins: [slider],

  data() {
    return {
      isPC,
      isMobile,
      isTablet,
      tabIndex: 0,
      item_list: [
        {
          type: 2,
          title: '台積電設廠旋風再起！楠梓這案公開19天實登即137戶引注目',
          subtitle: '',
          date: '2024.02.23',
          author: '文/記者林耀文',
          imgs: [
            {
              src: require('../newimg/26/01.jpg'),
              caption: '台積電預定在楠梓產業設置2座2奈米先進製程新廠，也將進行第3廠評估作業。受惠台積電串聯各大科學園區建設發展，北高雄近期再度成為高雄房市暢旺區，也讓鄰近台積電預定區的楠梓高大特區預售市場快速加溫。',
            },
            {
              src: require('../newimg/26/02.jpg'),
              caption: '高大特區指標型預售建案茂德機構「高大之森」，全案於去年底12月22日公開銷售，短短19天就銷售137戶已可實價登錄查詢。（圖：業者提供）',
            },
            {
              src: require('../newimg/26/03.jpg'),
              caption: '楠梓高大特區預售市場吸引眾多購屋置產客群注意，更引起全台科技新貴和企業主關注。（圖：業者提供）',
            },
            {
              src: require('../newimg/26/04.jpg'),
              caption: '市調顯示，農曆春節期間「高大之森」低調賣破200戶，相關周邊科技產業像是台積電、封裝大廠日月光工程師都默默低調團購預約。（圖：業者提供）',
            },
          ],
          desc:'台積電預定在楠梓產業設置2座2奈米先進製程新廠，也將進行第3廠評估作業。受惠台積電串聯各大科學園區建設發展，北高雄近期再度成為高雄房市暢旺區，包括鄰近台積電預定廠區的橋頭新市鎮與楠梓高大特區預售市場快速加溫，其中尤以台北建商南下推案吸引眾多購屋置產客群注意，更引起全台科技新貴和企業主關注，新台17線旁新案12月22日公開，短短19天實價登錄即137戶，成為市場話題。<br><br>近來高雄市政府在北高雄推動的多項重大建設，也為楠梓房市發展提供絕對助力，如高大特區西側，新台17線日後全線完工後僅約15分鐘左右，即可直達高鐵站、美術館特區；而未來捷運紫線更串聯高大特區和3座科學園區、5所學院，形成北高雄產-官-學-建設大動脈，而這裡，是所有交通要塞的交匯點。成為高雄剛性需求購屋客最佳買房增值選擇。<br><br>建築業界評估，台積電拍板高雄廠興建於楠梓產業園區，以園區基地規模來看，預計興建三座以上晶圓廠，擴大高雄楠梓投資發展，投資額上看2兆元，錯過竹科內科，從高大特區實價登錄均價可窺見，聰明購屋置產客以實際行動不再錯過台積電護盤讓財富翻倍良機。<br><br>最新市調顯示，高大特區指標型預售建案「高大之森」，是由北台灣前三大建商茂德機構插旗南台灣高雄第一站，業者為大台北重劃區推手，更看好楠梓區S科技廊帶正核心的高大特區，未來建設發展如同現在的竹科竹北高鐵特區和台北內科大直重劃區。<br><br>現場銷售專案經理林鈺浩表示，全案於去年底12月22日公開銷售，短短19天就銷售137戶，從實價登錄即可看到，更在農曆春節期間低調賣破200戶，相關周邊科技產業像是台積電、封裝大廠日月光工程師都默默低調團購預約，坐等先買先享受的漲幅空間，在台積電周邊設廠加持下，「高大之森」將會是南台灣最閃耀的科技重鎮房市指標之一。',
          links: [
            {
              title: '原文',
              id: '1',
              url: 'https://estate.ltn.com.tw/article/19758?fbclid=IwAR3zwg5ISDekfJ4pjOx7IvB1_1mcjyjWNTzISOArYdu0EMDHvsppQ4X9MqI',
            },
            {
              title: '高大之森',
              id: '2',
              url: 'https://sigma.debang.tw/',
            },
          ],
        },
        {
          type: 2,
          title: '【廣編】台積電來高雄威力太強　楠梓建案實登19天熱賣137戶',
          subtitle: '',
          date: '2024.02.22',
          author: '圖、文／高大之森提供',
          imgs: [
            {
              src: require('../newimg/25/1.jpg'),
              caption: '受惠於台積電於高雄設廠發展，讓高大特區成為房地產熱區，其中「高大之森」受到許多置產族青睞。',
            },
          ],
          desc:'台積電2奈米先進製程將在高雄楠梓園區增設第2廠，不排除第3廠，房價回不去了。受惠於台積電串聯各大科學園區建設發展，北高雄成高雄房市暢旺區，而身處在S科技廊帶正核心的楠梓已成為最熱門區域，其中尤以台北建商南下推案吸引眾多購屋置產客群注意，12月22日公開短短19天從實價登錄就可發現已銷售137戶，且在政府政策作多下，交通建設逐步到位，科技大廠和政府行政部門紛紛佈局設點，楠梓高大特區儼然成為南霸天置產熱區，承襲竹科昔日置產榮景。<br><br>台積電拍板高雄廠興建於楠梓產業園區，以園區基地規模來看，預計興建三座以上晶圓廠，擴大高雄楠梓投資發展，投資額上看兆元。高雄市政府多項重大建設也為楠梓發展提供絕對助力，如高大特區西側，新台17線僅15分鐘內可直達高鐵站、美術館特區；而未來捷運紫線更串聯高大特區和3座科學園區、5所學院，形成北高雄產-官-學-建設大動脈，而這裡，是所有交通要塞的交匯點，成為高雄剛性需求購屋客最佳買房增值選擇。<br><br>高大特區指標建案「高大之森」北台灣前三大建商茂德機構插旗南台灣高雄第一站，大台北重劃區推手看好楠梓區S科技廊帶正核心的高大特區，未來建設發展如同現在的竹科竹北高鐵特區和台北內科大直重劃區，現場銷售專案經理林鈺浩表示，農曆春節期間低調賣破200戶，相關周邊科技產業像是台積電、封裝大廠日月光工程師都默默低調團購預約，坐等先買先享受的漲幅空間。',
