          type: 2,
          title: '洲子洋重劃區親民價！「快樂王國」輕巧坪數2至3房成置產標的',
          subtitle: '',
          date: '2023.12.04',
          author: '記者李彥慧／採訪報導',
          imgs: [
            {
              src: require('../newimg/23/1.jpg'),
              caption: '位在洲子洋重劃區的「快樂王國-理享城」具備六大優勢，成為購屋族置產標的！',
            },
            {
              src: require('../newimg/23/2.jpg'),
              caption: '位在洲子洋重劃區的「快樂王國-理享城」具備六大優勢，成為購屋族置產標的！',
            },
            {
              src: require('../newimg/23/3.jpg'),
              caption: '「快樂王國-理享城」周邊擁有424公頃大都會公園和8800坪體健公園，綠意環繞。',
            },
            {
              src: require('../newimg/23/4.jpg'),
              caption: '「快樂王國-理享城」周邊擁有424公頃大都會公園和8800坪體健公園，綠意環繞。',
            },
            {
              src: require('../newimg/23/5.jpg'),
              caption: '坪數規劃為16到33坪2至3房，讓首購、置產族群都能輕鬆入手！',
            },
            {
              src: require('../newimg/23/6.jpg'),
              caption: '坪數規劃為16到33坪2至3房，讓首購、置產族群都能輕鬆入手！',
            },
          ],
          desc:isMobile?
            '台灣高物價環境，讓許多人開始選擇置產抗通膨。而位於新北市五股的洲子洋重劃區，擁有絕佳地理位置和親民房價，成為雙北人爭相搶進的置產標的！專案經理朱可天表示，洲子洋重劃區目前房價5字頭，相較於三重、蘆洲動輒要七、八十萬，洲子洋重劃區未來房價仍具補漲空間，成為購屋族最佳置產標的！<br><br>專案經理朱可天表示，位於洲子洋重劃區的「快樂王國-理享城」具備六大優勢，包括重劃、水岸、捷運、公園、商圈，以及學區等多重利多，開車三分鐘即可上台64線，銜接國道一號、台1線、台65線，以及新北環快，皆可在五分鐘內抵達，而且走路3分鐘，還能到達五泰板輕軌F06站，只要再搭一站就能到達蘆洲捷運站，交通相當便利。<br><br>「快樂王國-理享城」不僅交通便捷，生活機能鄰近成泰路雙商圈，周邊擁有424公頃大都會公園和8800坪體健公園，綠意環繞，市場稀缺的捷運公園宅成為現代人置產首選。更邀請到三大名設計師，賦予快樂王國「英式庭園渡假風」，是洲子洋最耀眼年度地標鉅作！<br><br>朱可天提到，「快樂王國-理享城」得到國際四個大獎的肯定，最知名的是有奧斯卡建築界之稱的「美國MUSE設計獎」，搭配五大防疫系統建材，公共設施請到國際知名設計，搭配21項近千坪的花園私人六星級會所，讓親子可以在最健康、最快樂的環境下生活，擁抱夢想在理享城中成長。「快樂王國-理享城」的坪數規劃為16到33坪、2至3房，輕巧坪數及新式零雨遮設計使空間感加倍，讓首購、置產族群都能輕鬆入手！<br><iframe style="width:100%;height:61vw" src="https://www.youtube.com/embed/-F79dgVBV80?si=heTVbOede8mD5-uZ" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowfullscreen></iframe>':'台灣高物價環境，讓許多人開始選擇置產抗通膨。而位於新北市五股的洲子洋重劃區，擁有絕佳地理位置和親民房價，成為雙北人爭相搶進的置產標的！專案經理朱可天表示，洲子洋重劃區目前房價5字頭，相較於三重、蘆洲動輒要七、八十萬，洲子洋重劃區未來房價仍具補漲空間，成為購屋族最佳置產標的！<br><br>專案經理朱可天表示，位於洲子洋重劃區的「快樂王國-理享城」具備六大優勢，包括重劃、水岸、捷運、公園、商圈，以及學區等多重利多，開車三分鐘即可上台64線，銜接國道一號、台1線、台65線，以及新北環快，皆可在五分鐘內抵達，而且走路3分鐘，還能到達五泰板輕軌F06站，只要再搭一站就能到達蘆洲捷運站，交通相當便利。<br><br>「快樂王國-理享城」不僅交通便捷，生活機能鄰近成泰路雙商圈，周邊擁有424公頃大都會公園和8800坪體健公園，綠意環繞，市場稀缺的捷運公園宅成為現代人置產首選。更邀請到三大名設計師，賦予快樂王國「英式庭園渡假風」，是洲子洋最耀眼年度地標鉅作！<br><br>朱可天提到，「快樂王國-理享城」得到國際四個大獎的肯定，最知名的是有奧斯卡建築界之稱的「美國MUSE設計獎」，搭配五大防疫系統建材，公共設施請到國際知名設計，搭配21項近千坪的花園私人六星級會所，讓親子可以在最健康、最快樂的環境下生活，擁抱夢想在理享城中成長。「快樂王國-理享城」的坪數規劃為16到33坪、2至3房，輕巧坪數及新式零雨遮設計使空間感加倍，讓首購、置產族群都能輕鬆入手！<br><iframe style="width:100%;height:39vw" src="https://www.youtube.com/embed/-F79dgVBV80?si=heTVbOede8mD5-uZ" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowfullscreen></iframe>',
