        {
          type: 2,
          title: '有台積電就是狂！高雄新案單月賣破500戶 銷售率37%全國最威',
          subtitle: '',
          date: '2024.01.25',
          author: '',
          imgs: [
            {
              src: require('../newimg/20/1.webp'),
              caption: '去年第4季預售屋市況呈現價穩量增。（圖／國泰房地產指數提供)',
            },
            {
              src: require('../newimg/20/2.webp'),
              caption: '北高雄如鼓山區豪宅聚落等，交易出色穩定。（圖／EBC地產王張琬聆攝）',
            },
            {
              src: require('../newimg/20/3.webp'),
              caption: '上漲過快的房價造成區域銷售的壓力。（圖／EBC地產王張琬聆攝）',
            },
          ],
          desc:
            '最新國泰房地產指數出爐，2023年第4季的預售屋市況，整體較上季及去年同期價穩量增，進一步觀察各都會區單月的表現，高雄市異軍突起，12月選前觀望期的30天銷售率高達37.62%，售出504戶佔全國約56%！專家指出，台積電話題熱度強，實質帶動房市發展。<br><br>根據國泰房地產指數2023年第4 季的市調資料分析，高雄未受總統大選影響延後推案，推案金額較上季仍增加2成多，成交量更創近3年新高，整體價量俱漲，房市熱絡。<br><br><h4 style="font-size:1.3em">高雄預售屋月賣504戶 佔全國銷量56%</h4>進一步觀察10~12月趨勢，高雄又以12月表現最令人刮目相看，其中的30天銷售率為37.62%，高過台南的30.59%；已銷戶數更高達504戶，與全國已銷901戶來看，佔比過半達55.94%，可說是去年底房市低迷時期的最火熱區域。<br><br>住展雜誌發言人陳炳辰指出，高雄市地位為南台灣之首，重要性一直與台北市南北相互輝映，包括捷運、亞灣區、產業園區等建設，加上近年台積電設廠效益，話題與實質上帶動房市發展。由於跟北中一線城市相比，房價基期仍低，都讓房市更具發揮空間，雖然鄰近的台南市近年也有南科議題，不過目前看來高雄還是略勝一籌。<br><br>陳炳辰分析，房市熱絡區以北高雄出色穩定，除豪宅聚落鼓山區、機能成熟且住宅量體大的三民區，還有台積電與園區話題熱度強、房價大幅攀升的楠梓區、橋頭區，以及有商圈與高鐵效益的左營區，都吸引建商大量推案；又如南高雄亞灣區，也有公共建設與港灣景觀利多，同樣助攻房市。<br><br>位於楠梓高大特區的［高大之森］，去年12月22日才開案，根據該案專案經理林鈺浩表示，「確實觀察到買方因台積電設廠而來的強勁購屋力道，事實上光是12月份就銷售117戶。」<br><br><h4 style="font-size:1.3em">過去台積電領頭羊 轉趨保守衰退</h4>不過若從全國整體表現來看，過去大吃台積電紅利的新竹、台南，去年第4季表現則相形遜色，房地產指數為全國少數價量俱跌、表現衰退的區域，原因就出在台積電題材導致房價狂飆後，造成買盤外溢到低價區、建商推案腳步放緩等現象。<br><br>相關新聞：暴漲區修正了！新竹、台南預售價量俱跌 國泰建設：缺利多維繫熱度<br><br>元宏不動產加值服務平台研究總監陳傑鳴表示，近幾年台灣主要都會區房價狂飆，其中受惠科學園區、科技大廠設廠等利多題材的新竹、台南，近5年房價上漲幅度都高達4~5成，在全台都會區中屬於領頭羊。<br><br>不過陳傑鳴話鋒一轉，「上漲過快的房價，目前已給區域銷售帶來壓力」，加上去年台灣出口額出現衰退、高科技高薪購屋族購買力減少的狀況下，建商推案已開始趨於保守，甚至有精華區價格推不上去，改往價格較低的新興重劃區推案，因此導致如台南推案平均價格出現修正的情形。<br><br><h4 style="font-size:1.3em">新青安拉抬買氣 想等價跌恐失望</h4>儘管暴漲區走到觀望保守格局，且想買房的民眾看到政府打炒房如《平均地權條例》修法、囤房稅2.0版、央行限貸令等政策與措施，都期望房價會下跌，但陳傑鳴也直言，實際上這幾年因房價大漲，建商普遍已經賺飽飽，建商都還相當能撐，只有少數財力較差的小建商才有機會出現降價。<br><br>加以目前政府蘿蔔跟棒子齊出，除了持續打炒房外，去年8月剛上路的「新青年安心成家購屋優惠貸款方案」也明顯提升了首購族的購屋能力，讓大量滯銷的首購宅起了相當程度的拉抬買氣作用，導致近期各區房價普遍仍以「漲勢趨緩」為主基調，想等房價下跌的民眾恐怕會失望。<br><br>陳炳辰也認為，最近房市在新青安、低自備、房市即將回溫等條件下明顯走強的機率高，若是價格還未起漲的區域，買賣兩造繼續激盪出火花都不意外。<br><br>（封面圖／EBC地產王 張琬聆攝）',
