        {
          type: 2,
          title: '【好房網】中和新成屋攀上8字頭！專家揭不敗原因：高總價也買單',
          subtitle: '',
          date: '2023.08.83',
          author: '好房網',
          imgs: [
            {
              src: require('../newimg/18/1.jpg'),
            },
            {
              src: require('../newimg/18/2.jpg'),
            },
          ],
          desc:
            '不論房市紛擾，目前多數區域的房價仍呈持平不跌格局，甚至不乏繼續創新區域高價的狀況。根據實價登錄資訊，新北市中和區今年出現2筆站上8字頭的住宅大樓交易，分別為中安街四號公園第一排的「綠INN」9樓戶，轉手單價達88萬元；景新街「太陽帝國」33樓高樓層戶別，單價83.1萬元。<br><br>觀察內政部不動產交易實價查詢服務網，中和區今年以來的買賣交易紀錄，扣除掉1樓做為店面的特殊產品，共有2筆站上8字頭的住宅大樓交易，其中「綠INN」9樓戶別今年6月以總價4260萬元轉手，換算單價88萬元，前屋主於2020年7月以總價3920萬元買入，等於持有3年帳面獲利340萬元。<br>新北市中和區今年出現8字頭的住宅交易記錄。圖／翻攝內政部不動產交易實價查詢服務網',
          links: [
            {
              title: '原文',
              id: '1',
              url: 'https://news.housefun.com.tw/news/article/amp/669012386469.html',
            },
          ],
