        {
          type: 2,
          title: '【鏡周刊】新北「這區」CP值高！親民房價擄獲大量首購族芳心',
          subtitle: '',
          date: '2022.10.26',
          author: '鏡周刊',
          imgs: [
            {
              src: require('../newimg/17/1.jpg'),
            },
            {
              src: require('../newimg/17/2.jpg'),
