              src: require('../newimg/17/4.jpg'),
            },
            {
              src: require('../newimg/17/5.jpg'),
            },
            {
              src: require('../newimg/17/6.jpg'),
            },
          ],
          desc:
            '疫情打亂生活步調，讓民眾待在家的時間變長，對住宅自然也更加要求。實地走訪發現，越來越多人為了兼顧健康與生活品質，選擇購入「健康精品宅」，除了擁有高綠覆率、景觀佳，更看中其與市區剛剛好的距離，不僅能兼顧生活機能，也能兼具生活品質。而今天要來開箱的建案「快樂王國」則是「健康精品宅」中的頂標，就坐落在424公頃的新北大都會公園正對面，它還有什麼特別之處？一起來看看！<br><br>424公頃公園正對面！捷運近在眼前好方便<br>位於新北市五股區的「快樂王國」相鄰7,600坪體健公園、1,200坪成德公園，且正對面即是424公頃的新北大都會公園，相當於16座大安森林公園，每日起床即被綠意植披、水岸綠地圍繞，下班後或是假日則可以暢遊7公里河濱行車道，是大台北地區最最珍貴的都會綠洲！<br><br>健康精品宅中的頂標「快樂王國」有多厲害？<br><br>你以為這樣的世外桃源，一定離市區非常遠嗎？其實捷運就近在眼前！「快樂王國」不僅綠覆率高，也因位於新蘆特區，周邊交通機能方便，擁有五泰輕軌F06站、蘆洲捷運站，另離台64線、國道1號、汐五高架、五楊高架等也很近，輕鬆串接台65、新北環快、台1甲快速道路等，走南往北都超方便！<br><br>三高、四捷、四特快讓「快樂王國」走南往北都方便！散步3分鐘即到五泰輕軌F06站、2分鐘上台64、5分鐘上國道一號。<br>絕佳生活機能！蘆洲銀河灣計畫後勢可期<br>除了交通便利，「快樂王國」更因為鄰近成泰路商圈，距離一橋之隔還有蘆洲長榮商圈，雙區生活圈讓生活採買超便利，最近的全聯也只要２分鐘，成洲市場、凌雲商圈也只需散步３分鐘。另外發展中的蘆洲銀河灣計畫也只有一橋之隔，不只有AI智能主題樂園、河堤巨幕，連萬坪蘆洲醫院也即將入駐，娛樂、醫療機能具足，將成就新北科技新都心。<br><br>如此可期的發展前景，外加親民的房價、鄰近學區等優勢，吸引許多首購族爭相入場。專案經理李祥琦表示：「因鄰近明星雙學區，像是五股國中、蘆洲國中、鷺江國中等都可供選擇，且21~30坪的坪數、2~3房格局、一層四戶規格，且戶戶邊間雙面採光，是難得的好物件。」<br><br>21-30坪，2-3房最剛好的規劃，且戶戶邊間雙面採光「快樂王國」是難得的好物件。<br>國際建築金獎肯定！全方位打造智慧潔淨社區<br>的確，「快樂王國」無論從地段選址、設計規劃都十分具有前瞻性。值得一提的是寶德建設不只嚴選建築團隊與國際建材，讓「快樂王國」成為最堅固的堡壘，更榮獲法國2022年NOVUM DESIGN AWARD設計金獎肯定，且在公設上還規劃全齡化多功能娛樂休閒會館、閱覽室、健身房、兒童遊戲區等一應俱全，在家就能像度假般享受。<br><br>迎賓大廳、閱覽室、健身房、兒童遊戲區等，「快樂王國」不只是最堅固的堡壘，也要帶給住戶最舒適的享受。<br><br>另外，設計上更納入AI智慧科技防疫概念，全方位守護家人健康。以五大防疫系統打造智慧潔淨社區，除了門廳有智慧防疫門，公設部分也有美國Honeywell空氣清淨機為健康把關，而電梯則是以永大防疫電梯搭配夏普空氣清淨機，且社區更是有美國Honeywell社區淨水器，全方位守護家人健康。',
          links: [
