        {
          type: 2,
          title: '【住展房屋網】外貌協會，從建案外觀就知道他貴—雙北5大特色建築榜，住過就回不去了',
          subtitle: '',
          date: '2022.09.30',
          author: '住展房屋網',
          imgs: [
            {
              src: require('../newimg/14/1.jpg'),
            },
            {
              src: require('../newimg/14/2.jpg'),
            },
            {
              src: require('../newimg/14/3.jpg'),
            },
            {
              src: require('../newimg/14/4.jpg'),
            },
          ],
          desc:
            '住宅建築外觀越蓋越漂亮，顏值越高的，光從外觀一看就知道房價不菲，近年更成為網美拍照打卡朝聖熱點，這些豪宅風格多樣，住展房屋網特別企劃『雙北5大特色住宅建築榜』，其中包含台北信義、天母、大安、新店等區豪宅，以及至今各案最高的實價登錄成交價供參考。<br><br>大安區：元利信義聯勤<br>首先看到的是「元利信義聯勤」位於26公頃的大安森林公園第一排，該案建築外觀找來普立茲克建築獎大師Richard Rogers操刀，以單純幾何線條展現剛強結構美學，極簡的外觀設計搭配金屬結構及橙紅色系結構玻璃的撞色處理，呈現強烈視覺衝擊效果，大膽、高冷、洗鍊的現代感十足吸睛，更獲得世界高層建築與都市人居學會CTBUH 100至199公尺的卓越獎，該案位於32樓層的實價登錄299萬元／坪，為目前最高成交價記錄。<br><br>信義區：琢白<br>同樣也是找來普立茲克建築獎大師Richard Meier設計建築外觀的「琢白」，位於信義區象山腳下，延續紐約白派一脈的白色立面設計風格。採幾何極簡線條搭配玻璃帷幕的清透感，風格鮮明，不僅如此建築外觀建材也很講究，使用頂級的飛機外殼鋁板烤漆，可謂現代主義經典藝術建築代表，平均開價280萬元／坪，而位於30樓層的實價登錄232萬元／坪，為目前最高成交價記錄。<br><br>士林區：華固天鑄<br>「華固天鑄」位於士林正天母，該案獲得2018年Good Design環境設計大獎，建築外觀由新加坡WOHA建築團隊及台灣簡俊卿建築師事務所操刀，將陽明山的陽光、綠地及空氣等元素融入其中，立面採用超大不對稱的矩形框架設計，搭配極細緻的中式鏤空屏風，從遠處望去彷彿縈繞於陽明山白雲仙境中，平均開價190萬元／坪，而位於37、38樓層的實價登錄185萬元／坪，為目前最高成交價記錄。<br><br>中山區：富富話合<br>不同於以上3宗的摩天大樓，中山區緊鄰康樂林森公園綠帶的「富富話合」以強烈的設計感深獲外貌協會購屋族的青睞，獲得2018年TRAA第6屆台灣住宅建築獎集合住宅類佳作，該案找來日本自然派建築師平田晃久擘劃，將建築打造成既有趣又前衛的有機量體，採大量斜屋頂立面設計，讓自然光影隨時間軸的轉變產生錯落變化。<br><br>每戶退縮規劃不僅創造出露台空間，隨著階梯式的走跳大片雨遮設計，向上退縮後彷彿該建築量體是由立方體錯層堆疊而起，更為其外觀帶來豐富的建築表情，同樣也吸引不少網美前來拍照打卡，像是莫莉molly、史丹×侑敏等，該案位於6樓層的實價登錄134.6萬元／坪，為目前最高成交價記錄。<br><br>新店區：碧波白<br>最後看到新北市新店區的「碧波白」，獲得美國謬思設計獎Muse Design Awards外觀設計、室內空間設計的雙金殊榮，創下台灣MUSE雙料金獎紀錄，該案前後共榮獲8個大獎。「碧波白」由澳洲百年建築團隊PTW與台灣空間美學大師王勝正攜手擘劃，景觀設計則出自大漢景觀設計園藝師徐世萃之手。<br><br>「碧」案鄰近碧潭水岸，近北新路商圈、光明商圈，為與碧潭風景區相呼應，建築團隊更取其碧潭風帆意象來打造建築外觀，採用白色波浪的有機律動曲線設計，帶出隨風擺動的波浪型體，有別於傳統建築剛硬立面設計，顯得格外活潑，吸引不少網美前來拍照打卡，像是小花姐接、吃貨購物狂的森SEN、重機妹周妍等。<br><br>該案目前平均開價83萬元／坪，而位於24樓層的實價登錄87.8萬元／坪，為目前最高成交價記錄，若與以上其他4宗特色建築相比可說是CP值中最高，幾乎北市半價即可入手，吸引不少北市隱形富豪相繼入手收藏。<br><br>以上這5款大台北特色住宅建築，因為有著高消費族群無法抗拒的外觀魅力，使得實登也著實讓人瞠目咋舌，嚇到吃手手，有興趣的購屋族不妨大膽前往朝聖，一探究竟吧。',
