          ],
          desc:
            '具有人文精神的藝術，不只能夠為生活帶來美感，更能為空間展現獨有的質感氛圍，因此如何將藝術融入生活其實是一門學問。今天透過呈境設計袁世賢總監的專業經驗，為我們剖析藝術的同時，也告訴我們將藝術融入生活的設計巧思有哪些，進一步思考藝術與空間設計的奇妙關係！<br><br>以藝術品或家飾精品形塑空間美感時，要考量的空間條件有哪些？<br>從感官的角度出發，整體空間中要考量的部分，包含格局、採光、動線、材質。<br><br>一、格局<br>透過了解各場域間的位置、大小，進而去挑選適當的藝術品，在大空間可以放大尺度的藝術品，小空間則可以放小尺度、精緻的藝術品。<br><br>二、採光<br>在視覺層面上，可以先挑選出合適的燈具與色溫，搭配照射角度，藉此可以突顯藝術品的特色，如線條、輪廓、色調。<br><br>三、動線<br>為了營造觀賞藝術品的最佳氛圍，必須先思考觀看時的路徑，考量行走時的節奏與步伐，妥善安排最佳動線。<br><br>四、材質<br>為了避免藝術品在視覺上受到影響，建議以乾淨、簡潔的質地去鋪陳背景，如此才能襯托出藝術品賦予空間美感的價值。<br><br>如何挑選與空間風格匹配的藝術品或家飾精品？<br>為了讓空間與藝術產生美好連結，先透過觀察空間的風格，再從空間的機能挑選合適的藝術品，比如書房的區域，如果想要增加藝文氣息，可以挑選有關文學、詩畫、書法字畫等類型；餐廳的區域，可以選擇食材雕塑、陶瓷器皿等類型。<br><br>在空間設計中喜歡搭配的家飾精品品牌有哪些？<br>藝術是人類文化的結晶，更是生活中調劑身心不可或缺的精神糧食，有鑑於此，從挑選讓自己滿意的家飾精品，就能替生活增添藝術的氣息，讓眼光獨到的袁世賢總監為我們推薦以下幾個家飾品牌。<br><br>來自義大利的Armani casa，擅長以天然材質的特性，透過精緻的表面收編技法，襯托出細膩的紋理，搭配設計靈感讓造型富有變化性，進一步將文化風采注入其中，流露出嶄新的面貌，同時又能蘊含經典的韻味。<br><br>令人耳目一新的落地花器，選自義大利品牌SERRALUNGA，以創新精神聞名，透過運用輕巧耐用的塑料材質，加上奇特的藝術美感與創作靈感，輕鬆為空間締造出新穎質感氣息。<br><br>吊燈的部分，則是來自荷蘭品牌MOOOI，跳脫傳統設計框架，以反向思維作為出發點，在探索設計的純粹性之餘，也以幽默、前衛、新奇的形式，為品牌創造出獨特性，如此絢爛的設計巧思，讓人著迷不已。<br><br>想要在藝術與設計中找到最佳平衡點，從考量空間本身的條件開始，透過視覺感知、氣氛感受，接著在風格尺度上選擇合適的藝術品或是家飾精品，一步步將生活與藝術產生連結，如此一來才能讓空間蘊含著滿滿的藝術情懷。',
