            {
              src: require('../newimg/12/2.jpg'),
            },
            {
              src: require('../newimg/12/3.jpg'),
            },
          ],
          desc:
            '今年供給量少的中和地區，受疫情、戰爭情勢、成本上漲等因素影響，上半年僅4個新推案，且自5月後來人下滑，據悉下半年新推案有8個以上，且中和房市無畏傳統淡季鬼月影響，線上指標案開價已站穩8字頭，甚有個案8成客源來自台北市，看來北客就愛吃這味。<br><br>以往鬼月一直是房地產的淡季，加上不少業者擔心裴洛西訪台後效應，影響房市買盤信心，不過根據建案現場人員回應，目前市場上尚未聽到有太大的反彈或影響，民眾至案場對此也較沒反應，至於一般剛性需求的買盤，在別無選擇下，影響程度有限，有意購屋民眾，現已無畏民俗忌諱了。<br><br>新案7指標8　北市半價不算貴<br>中和地區的房價，去年底平均行情已站穩5字頭，雖說今年上半年整個房市急凍，中和地區也不例外，上半年僅有4個新推案，包括「華鋐晴晨」、「擎天峰和」、「虹爵琢悅」及「華固得月」，其中「虹」及「華」案，平均開價都已來到7字頭。<br><br>值得關注的2個指標案，去年底新開案的「遠雄Park One」，平均開價65萬元／坪，規劃136坪的毛坯水岸宅，現已調漲至平均開價85萬元／坪，漲幅逾3成。轉成屋進場銷售的推案「太陽帝國」，平均開價也有85萬元／坪，實價登錄查到最高77.9萬/坪，規劃13至39坪的全棟鋼骨制震宅，訴求近捷運南勢角站，及整層豐富、CP值高的公設，該案台北市客源竟高達有7成。<br><br>據住展雜誌統計，台北市今年每月新推案平均開價都改寫歷史新高，7月新建案平均開價已漲至約118.3萬元／坪，其中信義、松山、大安等3區的平均開價，依序更高達163.6、159.9及157.4萬元／坪，因此若以台北市房價的一半，來購買新北市中和的房子並不算貴。<br><br>生活感如同　新北中和的台北市<br>觀察原因，中和地區鄰近永和、板橋、萬華等地，人口多且密度高，再加上近年捷運環狀線開通後，節省中和往返新店、新北產業園區間的上班運輸時間並提升運量，加上捷運附近生活機能完善，不少台北市購屋族來到捷運南勢角站周遭的生活圈，認為生活感如同在新北中和的台北市。<br><br>因此偏愛捷運周遭生活圈地段的物件，近期區內即使新建案少，舊案進入銷售尾聲，但內需供給仍大，由於在地中和人對於新建案的價格帶，認知在6字頭尾7字頭初，因此「太」案區內客僅佔3成。<br><br>受北客青睞除上述原因外，目前新推案的公設比約35至39％，而台北房市現階段主流推案產品為小基地面積的危老案，實際上住戶能使用的公設項目有限，而「太陽帝國」的公設僅33％，基地逾千坪，規劃一整層的私人公設，共計25項，疫情期間最受住戶喜歡的為健身房及廚藝教室，近期還預計邀請老師至飛輪、瑜珈教室授課，且社區每周都有舉辦活動。<br><br>今年下半年中和地區推案，可說是非常熱鬧，除醞釀中「遠雄三井中和案」外，未來陸續還有「龍瑩中山路案」、「漢皇里寓」、「雙和聚富」、「永邦圓通路案」、「欣聯南工段(和心)」等，值得期待。',
